.home {
  background-color: #000000;
  flex-grow: 1;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home .title {
  width: 100%;
  font-size: 18px;
  text-align: center;
}

.home .title p {
  font-size: larger;
  line-height: 50px;
  color: #bbbbbb;
}

.home .title button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 24px;
  margin-top: 30px;
  color: #141414;
  padding: 10px 15px;
  border-radius: 999px;
  background-color: lightgray;
  font-family: 'Oswald', sans-serif;
}

.home .person {
  width: 100%;
  margin-top: 20px;
}

.home .person img {
  width: 70%;
  padding-left: 75px;
  
  
}

@media (min-width: 800px) {
  .home {
    flex-direction: row;
    justify-content:space-between;
  }
  .home .title,
  .home .person {
    width: 50%;
    margin-top: 0;
    text-align: left;
    font-size: 25px;
  }
  .home .person img {
    width: 100%;
    
    
    
  }
}

